body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #46bb94;
}

.noises-container {
  display: flex;
  max-width: 1000px;
  flex-wrap: wrap;
  gap: 1.5em;
  justify-content: center;
  align-items: center;
  margin: 10em auto
}

.noises-container .noise {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  color: #ffffff88;
  position: relative;
  width: 10em;
}

.noises-container .noise.selected {
  color: #fff;
}
.noises-container .noise:hover {
  color: #fff;
  cursor: pointer;
}

.noises-container .noise div.input {
  height: 1em;
}
.noises-container input[type='range'] {
  appearance: none;
  width: 100%;
  height: .2rem;
  border-radius: .2rem;
}
.noises-container input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}
.noises-container input[type='range']::-moz-range-thumb {
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.noises-container .noise-volume {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 10em;
}

.noises-container .video-container {
  position: fixed;
  bottom: 0;
  left: 0;
}

.noises-container .video-container iframe {
  resize: both;
  width: 50vw;
}